.contact-body {
	margin: auto;
	position: fixed;
	width: 100%;
	height: 100%;
	justify-content: center;
	padding-top: 2em; }

.contact-grid {
	text-align: center;
	justify-content: center;
	width: 80%;
	height: 550px;
	background: white;
	color: black;
	margin-bottom: 20px; }

.contact-grid h2 {
	font-family: 'Anton', sans-serif; }

.contact-grid p {
	font-family: 'Oxygen', sans-serif;
	font-weight: bold; }

.textDescription {
    width: 75%;
    margin: auto;
    padding-top: 1em;
    text-align: justify; }

.contact-grid hr {
	border-top: 5px dotted black;
	width: 50%;
	margin: auto; }

.contact-list i {
	font-size: 66px;
	padding-right: 5rem; }

.item-list-contact {
    font-size: 24px;
    font-family: 'Anton'; }
